.status-wrap {
  max-width: 360px;
  width: 100%;
  margin: 60px auto;
  @include sm {
    margin: 40px auto; } }

.status {
  position: relative;
  display: block;
  min-height: 185px;
  border-radius: 60px;
  overflow: hidden;
  margin-bottom: 40px;
  text-align: center;
  padding: 10px;
  @include sm {
    min-height: 150px; }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 250px;
    height: 250px;
    background: url('../img/lines.png') no-repeat;
    background-size: contain;
    @include sm {
      width: 200px;
      height: 200px; } }
  &__text {
    margin-top: -11px;
    font-size: 18px;
    color: #fff;
    @include futura-b; }
  &__in {
    &:before {
      content: '';
      display: block;
      margin: 0 auto;
      vertical-align: middle;
      background-size: cover;
      background-repeat: no-repeat;
      width: 160px;
      height: 120px;
      @include sm {
        width: 120px;
        height: 86px; } } }
  &.is-error {
    background-image: linear-gradient(to right, #E2639A, #FF6974);
    box-shadow: 0 -33px 74px 0 rgba(#DD5EA3, .58);
    .status__in {
      &:before {
        background-image: url('../img/error.svg'); } } }
  &.is-success {
    background-image: linear-gradient(to right, #9CE660, #D8F796);
    box-shadow: 0 -26px 59px 0 rgba(#B3F168, .5);
    .status__in {
      &:before {
        background-image: url('../img/done.svg'); } } } }
