.slider {
  position: relative;
  max-width: 556px;
  margin: 100px auto 60px;
  text-align: center;
  @include md {
    max-width: 494px;
    margin: 80px auto 30px; }
  @include sm {
    max-width: 350px;
    margin: 40px auto; }
  &__arr {
    position: absolute;
    top: calc(50% - 15px);
    transform: translateY(-50%);
    display: inline-block;
    vertical-align: middle;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    border: 1px solid #fff;
    text-align: center;
    line-height: 60px;
    @include md {
      width: 48px;
      height: 48px;
      line-height: 50px; }
    @include sm {
      display: none !important; }
    &:hover {
      border-color: $pink;
      .icon {
        color: $pink; } }
    .icon {
      width: 10px;
      height: 16px;
      color: #fff;
      margin-right: -2px;
      @include md {
        width: 9px;
        height: 14px; } } }
  &__arr-left {
    right: calc(100% + 35px);
    .icon {
      transform: rotate(180deg);
      margin-left: -2px; } }
  &__arr-right {
    left: calc(100% + 35px); }
  &__item {
    position: relative;
    z-index: -1;
    margin: 0 10px;
    img {
      transform: translateX(10%) scale(.85, .85);
      transition: all 500ms ease-in-out;
      object-fit: cover; }
    &.slick-active {
      position: relative;
      z-index: 2;

      img {
        transform: translateX(0) scale(1, 1);
        box-shadow: 0 1px 13px 0 rgba(#000, .2); } }
    &.slick-active + .slider__item {
      opacity: 1 !important; } }
  .slick-dots {
    display: inline-block;
    vertical-align: middle;
    margin: 0 auto;
    button {
      font-size: 0;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: rgba(#fff, .3); }
    li {
      display: inline-block;
      vertical-align: middle;
      margin: 0 11px;
      &.slick-active {
        button {
          background: #fff; } } } } }
