.desc {
  &__caption {
    margin-bottom: 15px;
    @include md {
      max-width: 489px;
      margin: 0 auto 15px; } }
  &__img {
    position: relative;
    display: inline-block;
    margin-bottom: 30px;
    border-radius: 70px;
    width: 721px;
    max-width: 100%;
    height: 128px;
    background-image: url('../img/steam-profile.png');
    background-size: cover;
    @include md {
      max-width: 640px; }
    @include sm {
      max-width: 330px;
      margin-left: -15px;
      margin-right: -15px;
      border-radius: 0;
      height: 284px;
      background-image: url('../img/steam-profile-mob.png');
      background-size: contain; } }
  &__img_shadow {
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 9;
      box-shadow: inset  0 1px 56px 0 rgba($green, .73);
      border-radius: 70px;
      @include sm {
        border-radius: 0; } } }
  &__profile {
    position: absolute;
    left: 56px;
    top: 78px;
    z-index: 1;
    @include md {
      left: 50px;
      top: 67px; }
    @include sm {
      left: 12px;
      top: 128px; } } }

.profile {
  min-width: 120px;
  text-align: left;
  background: rgba(29, 38, 48, 100);
  @include clr;
  @include sm {
    min-width: 165px;
    background: #201f2a; }
  &__pic {
    float: left;
    margin-right: 10px;
    width: 28px;
    height: 28px;
    border: 2px solid gray;
    @include sm {
      width: 34px;
      height: 34px; }
    img {
      max-width: 100%;
      object-fit: cover; } }
  &__text {
    display: inline-block;
    vertical-align: top;
    letter-spacing: .5px;
    font-size: 12px;
    color: #fff;
    @include roboto-m;
    font-weight: 600; } }
