h1,
.h1 {
  font-size: 48px;
  color: #fff;
  letter-spacing: 2px;
  margin-bottom: 10px;
  @include futura-b;
  @include md {
    font-size: 38px; }
  @include sm {
    font-size: 28px; } }

h5,
.h5 {
  font-size: 18px;
  @include futura-b;
  @include md {
    max-width: 430px;
    margin: 0 auto; }
  @include sm {
    font-size: 14px; } }


.title {
  font-size: 18px;
  color: rgba(#fff, .59);
  @include futura-b;
  .icon-load {
    display: inline-block;
    vertical-align: sub;
    color: $green;
    margin-right: 4px; } }


h1 + .title {
  margin-bottom: 60px;
  @include md {
    max-width: 489px;
    margin: 0 auto 20px; } }
